html body #content {
  padding-top: 6rem;
  padding-bottom: 3rem;
}

.card-light {
  border: none;
}

.card-light .card-body {
  padding: 0;
}

.card-icon {
  font-size: 2em;
  text-align: center;
  padding-top: 1.25rem;
}

.portletSiteSetup {
  margin: 0 -15px 20px -15px;
}

.portletSiteSetup .portletHeader {
  color: #000;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 15px;
  padding: 0 15px;
}

.portletSiteSetup .portletContent .configlets {
  list-style: none;
  margin: 0;
  padding: 0;
}

.portletSiteSetup .portletContent .configlets li {
  margin-bottom: 30px;
}

.portletSiteSetup .portletContent .configlets li .inner-configlet {
  background-color: #fff;
  border: 1px solid #0083be;
  border-radius: 0.25rem;
  font-weight: 600;
  height: 100%;
  text-align: center;
  transition: all 80ms ease-in-out;
  width: 100%;
  word-break: break-word;
}

.portletSiteSetup .portletContent .configlets li .inner-configlet:hover {
  background-color: #0083be;
  border: 1px solid transparent;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
}

.portletSiteSetup .portletContent .configlets li .inner-configlet:hover a {
  color: #f8f9fa !important;
}

.portletSiteSetup .portletContent .configlets li .inner-configlet a {
  display: block;
  height: 100%;
  padding: 70px 10px 20px 10px;
  position: relative;
}

.portletSiteSetup .portletContent .configlets li .inner-configlet a span {
  background-color: rgba(255, 255, 255, 0.15);
  display: block;
  height: 60px;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.portletSiteSetup .portletContent .configlets li .inner-configlet a span:before {
  font-size: 32px;
  line-height: 60px;
  margin: 0;
  width: inherit;
}

.addon-status-icon {
  font-size: 60px;
  line-height: 0;
}

.addon-status-icon i {
  margin: 0;
  padding: 0;
}

.portletSiteSetup .portletHeader {
  text-align: center;
}

.section-heading {
  font-size: 1.25rem;
  font-weight: bold;
  line-height: 1.2;
  margin-bottom: 1rem;
}

#section-image {
  margin: 2rem 0;
}

#section-details {
  margin: 2rem 0;
}

#not-found,
#privileges {
  background-color: #0083be;
}

#not-found .inner-content-wrapper,
#privileges .inner-content-wrapper {
  align-items: center;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  max-width: 640px;
  min-height: 80vh;
  padding: 80px 0;
  text-align: center;
}

#not-found .inner-content-wrapper .inner-content,
#privileges .inner-content-wrapper .inner-content {
  padding: 0 30px;
  width: 100%;
}

#not-found .inner-content-wrapper .inner-content .inner-image,
#privileges .inner-content-wrapper .inner-content .inner-image {
  height: auto;
  margin-bottom: 30px;
  max-width: 150px;
}

#not-found .inner-content-wrapper .inner-content .inner-title,
#privileges .inner-content-wrapper .inner-content .inner-title {
  color: #f8f9fa;
  font-size: 18px;
  margin-bottom: 15px;
}

#not-found .inner-content-wrapper .inner-content .inner-text,
#privileges .inner-content-wrapper .inner-content .inner-text {
  color: #f8f9fa;
}

#not-found .inner-content-wrapper .inner-content .inner-text a,
#privileges .inner-content-wrapper .inner-content .inner-text a {
  color: #212529;
}

#not-found .inner-content-wrapper .inner-content .inner-text pre,
#privileges .inner-content-wrapper .inner-content .inner-text pre {
  background-color: #f8f9fa;
  border-radius: 0.25em;
  font-size: 12px;
  padding: 15px;
  text-align: left;
  white-space: pre-wrap;
}

#not-found .inner-content-wrapper .inner-content .inner-list,
#privileges .inner-content-wrapper .inner-content .inner-list {
  color: #f8f9fa;
  font-size: 14px;
  list-style: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

#not-found .inner-content-wrapper .inner-content .inner-list li,
#privileges .inner-content-wrapper .inner-content .inner-list li {
  margin-bottom: 10px;
}

#not-found .inner-content-wrapper .inner-content .inner-list li a,
#privileges .inner-content-wrapper .inner-content .inner-list li a {
  color: #212529;
}

.event ul {
  list-style-type: none;
